import React from "react";
import { useParams } from "react-router-dom";
import NavbarModule from '../commonsModule/navbarModule';
import DialogsModule from '../commonsModule/dialogsModule';
import ButtonsModule from '../commonsModule/buttonsModule';
import LoadingModule from '../commonsModule/loadingModule';
import HelpModule from '../commonsModule/helpModule';
import line from '../../img/vertical-line.svg';

// import css
import { makeStyles, withStyles } from "@material-ui/core/styles";

// material
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

// services
import BaseService from '../services/baseService';
import AbmService from '../services/abmService';

// start css
const useStyles = makeStyles((theme) => ({

    root: { flexGrow: 1, borderRadius: 0 },
    paddingLine: { padding: '0 16px', marginBottom: 5 },
    spaceText: { paddingLeft: theme.spacing(2) },
    gutterBottom: { marginBottom: 0 },
    line: { width: 6, paddingRight: 15, height: 16 },
    divider: { marginTop: 10, marginBottom: 10 },
    // formControl: { width: '50%', margin: theme.spacing(1), minWidth: 120 },
    table: { minWidth: 700 },
    button: {
        color: 'black', textTransform: 'capitalize', fontWeight: '600', cursor: 'default',
        '&:hover': { backgroundColor: '#ededed', color: 'black' }
    },
    add: { padding: '3%', fontSize: '14px', textTransform: 'uppercase' },
    selectEmpty: { marginTop: theme.spacing(2) },
    secondary: { backgroundColor: '#DB0011', color: 'white', fontWeight: 'bold' },
    success: { backgroundColor: '#4caf50', color: 'white', fontWeight: 'bold' },

}));

const StyledTableCell = withStyles((theme) => ({

    head: { backgroundColor: '#ededed', color: theme.palette.common.black, fontWeight: 'bold', paddingBottom: 6, paddingTop: 6 },
    body: { fontSize: 14, paddingTop: 5, paddingBottom: 5 }

}))(TableCell);

const StyledTableRow = withStyles((theme) => ({

    root: {
        '&:nth-of-type(odd)': {
            // backgroundColor: theme.palette.action.hover,
        },
    },

}))(TableRow);
// end css

export default function ChangeAddressTC() {

    // Entry params
    let { operationId, option, documentType, documentNumber, businessName, contactModeCode, productCode, causeCode,
        reasonCode, companyCode, responsibleSector, registerSector, user, origin,
        initContact, closeContact, productNumber } = useParams();

    // State variables
    const [disable, setDisable] = React.useState(false);
    const [firstOpen, setFirstOpen] = React.useState(false);
    const [secondOpen, setSecondOpen] = React.useState(false);
    const [resultRequest, setResultRequest] = React.useState("");
    const [resultStatus, setResultStatus] = React.useState("");
    const [resultMsg, setResultMsg] = React.useState("");
    const [loading, setLoading] = React.useState(false);
    const [age, setAge] = React.useState('');

    const handleChange = (event) => {
        setAge(event.target.value);
    };

    // Style variables
    const classes = useStyles();

    // Funcion que trae el mensaje del servicio


    // Funcion para guardar la registracion generada
    const saveData = () => {
        setFirstOpen(false);
        setDisable(true);
        setLoading(true);

        AbmService.saveAbm(operationId, productCode, causeCode, companyCode, documentType, documentNumber, productNumber, origin,
            user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, "", "")
            .then(data => {
                let pedido = data.registration.requestNumber;
                let mensaje = data.registration.message;
                let estado = data.registration.status;
                setSecondOpen(true);
                setResultRequest(pedido);
                setResultStatus(estado);
                setResultMsg(mensaje);
                setLoading(false);
            });
    }

    const getConfirmation = () => {
        setFirstOpen(true);
    }

    const printData = () => {
        setLoading(true);

        BaseService.printData(operationId, productCode, causeCode, companyCode, documentType, documentNumber, productNumber, origin,
            user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, "",
            "", "", "", resultRequest)
            .then(data => {
                setLoading(false);
            });
    };

    return (
        <div className={classes.root}>
            {/* Start Navbar Module */}
            <NavbarModule />
            {/* End Navbar Module */}

            <Container maxWidth="lg">
                <Grid container>
                    <Grid item lg={12} style={{ width: '100%' }}>
                        <Card className={classes.root} variant="outlined">
                            <CardContent>
                                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                    <img src={line} className={classes.line} alt="logo" />
                                    Cliente
                                </Typography>

                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={4}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Apellido y Nombre
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            {businessName}
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={4}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Tipo de Documento
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            {documentType}
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={4}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            N de Documento
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            {documentNumber}
                                        </Typography>
                                    </Grid>
                                </Grid>

                                <Divider variant="middle" className={classes.divider} />

                                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                    <img src={line} className={classes.line} alt="logo" />
                                        Lista de Tarjetas - Producto: TC - Visa: 0010001693
                                </Typography>

                                <TableContainer component={Paper}>
                                    <Table className={classes.table} aria-label="customized table">
                                        <TableHead>
                                            <TableRow>
                                                <StyledTableCell>Tipo</StyledTableCell>
                                                <StyledTableCell>Tipo Doc.</StyledTableCell>
                                                <StyledTableCell>N Doc.</StyledTableCell>
                                                <StyledTableCell>Apellido y Nombre</StyledTableCell>
                                                <StyledTableCell>Nmero</StyledTableCell>
                                                <StyledTableCell>Estado</StyledTableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody>
                                            {/* {debitos.map((debito) => ( */}
                                            <StyledTableRow>
                                                <StyledTableCell>Titular</StyledTableCell>
                                                <StyledTableCell>{documentType}</StyledTableCell>
                                                <StyledTableCell>{documentNumber}</StyledTableCell>
                                                <StyledTableCell>{businessName}</StyledTableCell>
                                                <StyledTableCell>{productNumber}</StyledTableCell>
                                                <StyledTableCell>Operativa</StyledTableCell>
                                            </StyledTableRow>
                                            {/* ))} */}
                                        </TableBody>
                                    </Table>
                                </TableContainer>

                                <Divider variant="middle" className={classes.divider} />

                                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                    <img src={line} className={classes.line} alt="logo" />
                                    Domicilio Actual
                                </Typography>

                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={12}>
                                        <Typography className={classes.pos} color="textSecondary">
                                            Av. Nazca 3103, Piso 3, Depto C. C.P: 1417, CABA. Tel: 11 1234567
                                        </Typography>
                                    </Grid>
                                </Grid>

                                <Divider variant="middle" className={classes.divider} />

                                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                    <img src={line} className={classes.line} alt="logo" />
                                    Domicilio Correspondencia
                                </Typography>

                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={12}>
                                        <Typography className={classes.pos} color="textSecondary">
                                            Av. Nazca 3103, Piso 3, Depto C. C.P: 1417, CABA
                                        </Typography>
                                    </Grid>
                                </Grid>

                                <Divider variant="middle" className={classes.divider} />

                                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                    <img src={line} className={classes.line} alt="logo" />
                                    Domicilio Nuevo
                                </Typography>

                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={3} style={{ paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Tipo de Domicilio
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <FormControl variant="outlined" size="small" className={classes.formControl}>
                                                <Select
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"
                                                    value={age}
                                                    onChange={handleChange}
                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    <MenuItem value={10}>Ten</MenuItem>
                                                    <MenuItem value={20}>Twenty</MenuItem>
                                                    <MenuItem value={30}>Thirty</MenuItem>
                                                </Select>
                                            </FormControl>
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            LOD
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <FormControl variant="outlined" size="small" className={classes.formControl}>
                                                <Select
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"
                                                    value={age}
                                                    onChange={handleChange}
                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    <MenuItem value={10}>Ten</MenuItem>
                                                    <MenuItem value={20}>Twenty</MenuItem>
                                                    <MenuItem value={30}>Thirty</MenuItem>
                                                </Select>
                                            </FormControl>
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={6} style={{ paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Calle
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>
                                </Grid>
                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={3} style={{ paddingTop: '15px', paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Nmero
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px', paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Piso
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px', paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Depto
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px', paddingBottom: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Cdigo Postal
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>
                                </Grid>
                                <Grid container spacing={6} className={classes.spaceText}>
                                    <Grid item lg={3} style={{ paddingTop: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Cdigo Geogrfico
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <FormControl variant="outlined" size="small" className={classes.formControl}>
                                                <Select
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"
                                                    value={age}
                                                    onChange={handleChange}
                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    <MenuItem value={10}>Ten</MenuItem>
                                                    <MenuItem value={20}>Twenty</MenuItem>
                                                    <MenuItem value={30}>Thirty</MenuItem>
                                                </Select>
                                            </FormControl>
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Telfono
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Localidad
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <TextField id="outlined-basic" size="small" variant="outlined" />
                                        </Typography>
                                    </Grid>

                                    <Grid item lg={3} style={{ paddingTop: '15px' }}>
                                        <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                            Provincia
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary">
                                            <FormControl variant="outlined" size="small" className={classes.formControl}>
                                                <Select
                                                    labelId="demo-simple-select-outlined-label"
                                                    id="demo-simple-select-outlined"
                                                    value={age}
                                                    onChange={handleChange}
                                                    label="Age"
                                                >
                                                    <MenuItem value="">
                                                        <em>None</em>
                                                    </MenuItem>
                                                    <MenuItem value={10}>Ten</MenuItem>
                                                    <MenuItem value={20}>Twenty</MenuItem>
                                                    <MenuItem value={30}>Thirty</MenuItem>
                                                </Select>
                                            </FormControl>
                                        </Typography>
                                    </Grid>
                                </Grid>

                                <br></br>

                                {/* start help */}
                                <HelpModule />
                                {/* end help */}
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>

                {/* Start Loading Module */}
                <LoadingModule loading={loading}></LoadingModule>
                {/* End Loading Module */}

                {/* Start Dialog */}
                <DialogsModule saveData={saveData} resultRequest={resultRequest} resultStatus={resultStatus}
                    resultMsg={resultMsg} firstOpen={firstOpen} setFirstOpen={setFirstOpen} printScreen={printData}
                    secondOpen={secondOpen} setSecondOpen={setSecondOpen}></DialogsModule>
                {/* End Dialog */}

                {/* Start Buttons Module */}
                <ButtonsModule disable={disable} getConfirmation={getConfirmation}></ButtonsModule>
                {/* End Buttons Module */}
            </Container>
        </div>
    );
}