import React from "react";
import { useParams } from "react-router-dom";
import NavbarModule from '../commonsModule/navbarModule';
import DialogsModule from '../commonsModule/dialogsModule';
import ButtonsModule from '../commonsModule/buttonsModule';
import LoadingModule from '../commonsModule/loadingModule';
import HelpModule from '../commonsModule/helpModule';
import line from '../../img/vertical-line.svg';

// import css
import { makeStyles, withStyles } from "@material-ui/core/styles";

// icons
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';

// material
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Paper from '@material-ui/core/Paper';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Tooltip from '@material-ui/core/Tooltip';
import Fab from '@material-ui/core/Fab';
import VisibilityIcon from '@material-ui/icons/Visibility';

// services
import BaseService from '../services/baseService';
import AbmService from '../services/abmService';

// start css
const useStyles = makeStyles((theme) => ({

    root: { flexGrow: 1, borderRadius: 0 },
    line: { width: 6, paddingRight: 15, height: 16 },
    divider: { marginTop: 10, marginBottom: 10 },
    formControl: { width: '50%', margin: theme.spacing(1), minWidth: 120 },
    table: { minWidth: 700 },
    button: { color: 'black', textTransform: 'capitalize', fontWeight: '600', cursor: 'default',
              '&:hover': { backgroundColor: '#ededed', color: 'black' } },
    add: { padding: '3%', fontSize: '14px', textTransform: 'uppercase' },
    selectEmpty: { marginTop: theme.spacing(2) },
    paddingLine: { padding: '0 16px', marginBottom: 5 },
    spaceText: { paddingLeft: theme.spacing(2) },
    gutterBottom: { marginBottom: 0 },
    chip: { fontWeight: 'bold' },
    container: { display: 'flex', flexWrap: 'wrap' },
    textField: { marginLeft: theme.spacing(1), marginRight: theme.spacing(1), width: 200 },
    absolute: { height: 10, width: 'auto' },
    space: { margin: 10 },
    tableWidth: { minWidth: 100 }

}));

const StyledTableCell = withStyles((theme) => ({

    head: { backgroundColor: '#ededed', color: theme.palette.common.black, fontWeight: 'bold', paddingBottom: 6, paddingTop: 6 },
    body: { fontSize: 14, paddingTop: 5, paddingBottom: 5 }

}))(TableCell);

const StyledTableRow = withStyles((theme) => ({

    root: {
        '&:nth-of-type(odd)': {
            // backgroundColor: theme.palette.action.hover,
        },
    },

}))(TableRow);
// end css

export default function AdditionalUpper() {

    // Entry params
    let { operationId, option, documentType, documentNumber, businessName, contactModeCode, productCode, causeCode,
        reasonCode, companyCode, responsibleSector, registerSector, user, origin,
        initContact, closeContact, productNumber } = useParams();

    // State variables
    const [disable, setDisable] = React.useState(false);
    const [firstOpen, setFirstOpen] = React.useState(false);
    const [secondOpen, setSecondOpen] = React.useState(false);
    const [resultRequest, setResultRequest] = React.useState("");
    const [resultStatus, setResultStatus] = React.useState("");
    const [resultMsg, setResultMsg] = React.useState("");
    const [loading, setLoading] = React.useState(false);
    const [checked, setChecked] = React.useState(true);
    const [product, setProduct] = React.useState(false);
    const [dataTable, setDataTable] = React.useState(false);
    const [age, setAge] = React.useState('');
    
    const handleChange = (event) => {
        setAge(event.target.value);
        setChecked(event.target.checked);
    };

    // Style variables
    const classes = useStyles();

    // Funcion para guardar la registracion generada
    const saveData = () => {
        setFirstOpen(false);
        setDisable(true);
        setLoading(true);

        AbmService.saveAbm(operationId, productCode, causeCode, companyCode, documentType, documentNumber, productNumber, origin,
            user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, "", "")
            .then(data => {
                let pedido = data.registration.requestNumber;
                let mensaje = data.registration.message;
                let estado = data.registration.status;
                setSecondOpen(true);
                setResultRequest(pedido);
                setResultStatus(estado);
                setResultMsg(mensaje);
                setLoading(false);
            });
    }

    const getConfirmation = () => {
        setFirstOpen(true);
    }

    const printData = () => {
        setLoading(true);

        BaseService.printData(operationId, productCode, causeCode, companyCode, documentType, documentNumber, productNumber, origin,
            user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, "",
            "", "", "", resultRequest)
            .then(data => {
                setLoading(false);
            });
    };

    return (
        <div className={classes.root}>
            {/* Start Navbar Module */}
            <NavbarModule />
            {/* End Navbar Module */}

            <Container maxWidth="lg">
                <Grid container>
                    <Grid item lg={12} style={{ width: '100%' }}>
                        <Card className={classes.root} variant="outlined">
                            <CardContent>

                                { product ?
                                <div className={classes.spaceText}>
                                    <Chip className={classes.chip} label="El cliente no posee este producto" />
                                </div>
                                :
                                <div>
                                    <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                        <img src={line} className={classes.line} alt="logo" />
                                        Alta de Adicionales
                                    </Typography>

                                    <Grid container spacing={6} className={classes.spaceText}>
                                        <Grid item lg={3}>
                                            <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                                Tipo y N de Documento
                                            </Typography>
                                            <Typography className={classes.pos} color="textSecondary">
                                                {documentType} - {documentNumber}
                                            </Typography>
                                        </Grid>
                                    </Grid>

                                    <Divider variant="middle" className={classes.divider} />

                                    <Typography variant="h6" component="h2" className={classes.paddingLine}>
                                        <img src={line} className={classes.line} alt="logo" />
                                        Lista de Tarjetas Visa - Producto: {productNumber}
                                    </Typography>

                                    {/* start table data */}
                                    { dataTable ?
                                        <div className={classes.spaceText}>
                                            <Chip className={classes.chip} label="El cliente no posee este producto" />
                                        </div>
                                    : <div>
                                    <TableContainer component={Paper}>
                                        <Table className={classes.table} aria-label="customized table">
                                            <TableHead>
                                                <TableRow>
                                                    <StyledTableCell>Apellido y Nombre</StyledTableCell>
                                                    <StyledTableCell>N de Tarjeta</StyledTableCell>
                                                    <StyledTableCell>Estado</StyledTableCell>
                                                    <StyledTableCell>Tipo de Tarjeta</StyledTableCell>
                                                    <StyledTableCell>Tipo de Documento</StyledTableCell>
                                                    <StyledTableCell>N de Documento</StyledTableCell>
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                {/* {debitos.map((debito) => ( */}
                                                <StyledTableRow>
                                                    <StyledTableCell>{businessName}</StyledTableCell>
                                                    <StyledTableCell>{productNumber}</StyledTableCell>
                                                    <StyledTableCell>OPERATIVA</StyledTableCell>
                                                    <StyledTableCell>Adicional</StyledTableCell>
                                                    <StyledTableCell>{documentType}</StyledTableCell>
                                                    <StyledTableCell>{documentNumber}</StyledTableCell>
                                                </StyledTableRow>
                                                {/* ))} */}
                                            </TableBody>
                                        </Table>
                                    </TableContainer>
                                    </div>
                                    }
                                    {/* end table data */}

                                    <Divider variant="middle" className={classes.divider} />

                                    <Card className={classes.root} variant="outlined">
                                        <CardContent>
                                            <Grid container spacing={6} className={classes.spaceText}>
                                                <Grid item lg={2} style={{display: 'flex', alignItems: 'center'}}>
                                                    <Checkbox checked={checked} onChange={handleChange} 
                                                                inputProps={{ 'aria-label': 'primary checkbox' }} />
                                                    <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                                        Repactacin
                                                    </Typography>
                                                </Grid>
                                                <Grid item lg={10}>
                                                    <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                                                        Domicilio Actual
                                                    </Typography>
                                                    <Typography className={classes.pos} color="textSecondary">
                                                        Arenales 2838, Piso 4, Depto B, CP 1425, CAPITAL FEDERAL, CIUDAD AUTNOMA DE BUENOS AIRES
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                        </CardContent>
                                    </Card>

                                    <Divider variant="middle" className={classes.divider} />

                                    {/* start table data */}
                                    { dataTable ?
                                        <div className={classes.spaceText}>
                                            <Chip className={classes.chip} label="El cliente no posee este producto" />
                                        </div>
                                    : <div>
                                    <TableContainer component={Paper}>
                                        <Table className={classes.table} aria-label="customized table">
                                            <TableHead>
                                                <TableRow>
                                                    <StyledTableCell className={classes.tableWidth}>Tipo de Doc.</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>N de Doc.</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>CUIT/CUIL</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>Apellido</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>Nombre</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>Estado Civil</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>Sexo</StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>Fecha Nac.</StyledTableCell>
                                                    <StyledTableCell>Nacionalidad</StyledTableCell>
                                                    <StyledTableCell>% Lim.</StyledTableCell>
                                                    <StyledTableCell></StyledTableCell>
                                                    <StyledTableCell></StyledTableCell>
                                                </TableRow>
                                            </TableHead>
                                            <TableBody>
                                                {/* {debitos.map((debito) => ( */}
                                                <StyledTableRow>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <FormControl variant="outlined" className={classes.formControl}>
                                                            <Select value={age} onChange={handleChange} label="Age"
                                                                    size="small" style={{height: '40px'}}>
                                                                <MenuItem value="">
                                                                    <em>None</em>
                                                                </MenuItem>
                                                                <MenuItem value={10}>Ten</MenuItem>
                                                                <MenuItem value={20}>Twenty</MenuItem>
                                                                <MenuItem value={30}>Thirty</MenuItem>
                                                            </Select>
                                                        </FormControl>
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <TextField variant="outlined" size="small" />
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <TextField variant="outlined" size="small" />
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <TextField variant="outlined" size="small" />
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <TextField variant="outlined" size="small" />
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <FormControl variant="outlined" className={classes.formControl}>
                                                            <Select value={age} onChange={handleChange} label="Age"
                                                                    size="small" style={{height: '40px'}}>
                                                                <MenuItem value="">
                                                                    <em>None</em>
                                                                </MenuItem>
                                                                <MenuItem value={10}>Ten</MenuItem>
                                                                <MenuItem value={20}>Twenty</MenuItem>
                                                                <MenuItem value={30}>Thirty</MenuItem>
                                                            </Select>
                                                        </FormControl>
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <FormControl variant="outlined" className={classes.formControl}>
                                                            <Select value={age} onChange={handleChange} label="Age"
                                                                    size="small" style={{height: '40px'}}>
                                                                <MenuItem value="">
                                                                    <em>None</em>
                                                                </MenuItem>
                                                                <MenuItem value={10}>Ten</MenuItem>
                                                                <MenuItem value={20}>Twenty</MenuItem>
                                                                <MenuItem value={30}>Thirty</MenuItem>
                                                            </Select>
                                                        </FormControl>
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <form className={classes.container} noValidate>
                                                        <TextField type="date" variant="outlined"
                                                            className={classes.textField} InputLabelProps={{
                                                            shrink: true, }} size="small" />
                                                        </form>
                                                    </StyledTableCell>
                                                    <StyledTableCell className={classes.tableWidth}>
                                                        <FormControl variant="outlined" className={classes.formControl}>
                                                            <Select value={age} onChange={handleChange} label="Age"
                                                                    size="small" style={{height: '40px'}}>
                                                                <MenuItem value="">
                                                                    <em>None</em>
                                                                </MenuItem>
                                                                <MenuItem value={10}>Ten</MenuItem>
                                                                <MenuItem value={20}>Twenty</MenuItem>
                                                                <MenuItem value={30}>Thirty</MenuItem>
                                                            </Select>
                                                        </FormControl>
                                                    </StyledTableCell>
                                                    <StyledTableCell>
                                                        10
                                                    </StyledTableCell>
                                                    <StyledTableCell style={{ paddingTop: '1px', paddingBottom: '1px'}}>
                                                        <Tooltip title="click para ver ms" aria-label="add">
                                                            <VisibilityIcon fontSize="large" color="" />
                                                        </Tooltip>
                                                    </StyledTableCell>
                                                    <StyledTableCell style={{ paddingTop: '1px', paddingBottom: '1px'}}>
                                                        <Tooltip title="click para eliminar fila" aria-label="add">
                                                            <DeleteIcon fontSize="large" color="secondary" />
                                                        </Tooltip>
                                                    </StyledTableCell>
                                                </StyledTableRow>
                                                {/* ))} */}
                                            </TableBody>
                                        </Table>
                                    </TableContainer>
                                    </div>
                                    }
                                    {/* end table data */}

                                    <Tooltip title="click para agregar fila" className={classes.space}>
                                        <Fab color="secondary" className={classes.absolute}>
                                            <AddIcon fontSize="large" />
                                        </Fab>
                                    </Tooltip>

                                    <br></br>

                                    {/* start help */}
                                    <HelpModule />
                                    {/* end help */}
                                </div>
                                }

                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>

                {/* Start Loading Module */}
                <LoadingModule loading={loading}></LoadingModule>
                {/* End Loading Module */}

                {/* Start Dialog */}
                <DialogsModule saveData={saveData} resultRequest={resultRequest} resultStatus={resultStatus}
                    resultMsg={resultMsg} firstOpen={firstOpen} setFirstOpen={setFirstOpen} printScreen={printData}
                    secondOpen={secondOpen} setSecondOpen={setSecondOpen}></DialogsModule>
                {/* End Dialog */}

                {/* Start Buttons Module */}
                <ButtonsModule disable={disable} getConfirmation={getConfirmation}></ButtonsModule>
                {/* End Buttons Module */}
            </Container>
        </div>
    );
}