import React from "react";
import { useParams } from "react-router-dom";
import NavbarModule from '../commonsModule/navbarModule';
import HelpModule from '../commonsModule/helpModule';
import DialogsModule from '../commonsModule/dialogsModule';
import ButtonsModule from '../commonsModule/buttonsModule';
import LoadingModule from '../commonsModule/loadingModule';
import UploadFile from '../commonsModule/uploadFile';
import DocumentalRelation from "../commonsModule/docuRelationModule";
import line from '../../img/vertical-line.svg';

import CustomizedSnackbars from '../commonsModule/snackbarModule';
import ReactDOM from 'react-dom';

// import css - import styles from mui
import { makeStyles, withStyles } from "@material-ui/core/styles";

// material - import mui components
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

// services
import BaseService from '../services/baseService';
import RelatedAccountService from "../services/relatedAccountService";
import UtilsService from "../services/utilsService";
import { ZoomOutMapRounded } from "@material-ui/icons";
import { yellow } from "@material-ui/core/colors";

// start css
const useStyles = makeStyles((theme) => ({

  root: { flexGrow: 1, borderRadius: 0 },
  line: { width: 6, paddingRight: 15, height: 16 },
  divider: { marginTop: 10, marginBottom: 10 },
  paddingLine: { padding: '0 16px', marginBottom: 5 },
  spaceText: { paddingLeft: theme.spacing(2) },
  gutterBottom: { marginBottom: 0 },
  formControl: { width: '95%' },
  formControl2: { margin: theme.spacing(1), minWidth: 120 },
  selectEmpty: { marginTop: theme.spacing(2) },

}));
// styles table
const StyledTableCell = withStyles((theme) => ({

  head: { backgroundColor: '#ededed', color: theme.palette.common.black, fontWeight: 'bold', paddingBottom: 6, paddingTop: 6 },
  body: { fontSize: 14, paddingBottom: 5, paddingTop: 5 }

}))(TableCell);

const StyledTableRow = withStyles((theme) => ({

  root: {
      '&:nth-of-type(odd)': {
          // backgroundColor: theme.palette.action.hover,
      },
  },

}))(TableRow);
// end css

export default function RelatedAccountFunc() {

  // Entry params
  let { operationId, option, documentType, documentNumber, businessName, contactModeCode, productCode, causeCode,
    reasonCode, companyCode, responsibleSector, registerSector, user, origin,
    initContact, closeContact, productNumber } = useParams();

  // State variables
  const [disable, setDisable] = React.useState(false);
  const [firstOpen, setFirstOpen] = React.useState(false);
  const [secondOpen, setSecondOpen] = React.useState(false);
  const [resultRequest, setResultRequest] = React.useState("");
  const [resultStatus, setResultStatus] = React.useState("");
  const [resultMsg, setResultMsg] = React.useState("");
  const [resultAddFiles, setResultAddFiles] = React.useState("");
  const [loading, setLoading] = React.useState(true);
  const [rows, setRows] = React.useState([]);
  const [severity, setSeverity] = React.useState('');
  const [message, setMessage] = React.useState('');
  const [openSnackbar, setOpenSnackbar] = React.useState(false);
  const [productDescription, setProductDescription] = React.useState("");
  const [esPrimaria, setEsprimaria] = React.useState(false);
  const [dataRow, setDataRow] = React.useState([]);
  const [denom, setDenom] = React.useState("");
  // Inicio Add Files (Agregar en todas las funcionalidades)
  const [isAddFiles, setIsAddFiles] = React.useState(false);
  const [clientRelations, setClientRelations] = React.useState([]);
  const [productRelations, setProductRelations] = React.useState([]);  
  // Fin Add Files (Agregar en todas las funcionalidades)
    // Style variables
  const classes = useStyles();

  // Funcion para guardar la registracion generada
  const saveData = async (retry) => {
    setFirstOpen(false);
    setDisable(true);
    setLoading(true);
    

    let commonParams = UtilsService.getCommonParams(operationId, productCode, causeCode, companyCode, documentType, documentNumber, businessName, productNumber, origin,
      user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, retry ? resultRequest : null);


    let transactionalRequest = {}
    // Inicio Add Files (Agregar en todas las funcionalidades)
    if (isAddFiles) {
      commonParams.relTipoDocumentalCliente = clientRelations;
      commonParams.relTipoDocumentalProducto = productRelations;
      let productFiles = productRelations.filter(relation => relation.esAddFiles === true);
      commonParams.adjuntarArchivos = productFiles.length > 0;
    }
    // Fin Add Files (Agregar en todas las funcionalidades)
    transactionalRequest.relatedAccountParams = rows;
    transactionalRequest.commonParams = commonParams;

    // Inicio Add Files (Agregar en todas las funcionalidades)
    if (isAddFiles) {
      let data = await BaseService.saveDataWithFiles(transactionalRequest);
      let pedido = data.registration.requestNumber;
      let mensaje = data.registration.message;
      let mensajeAddFiles = data.registration.messageAddFiles;
      let estado = data.registration.status;
      setSecondOpen(true);
      setResultRequest(pedido);
      setResultStatus(estado);
      setResultMsg(mensaje);
      setResultAddFiles(mensajeAddFiles);
      setLoading(false);
    } else {
      BaseService.saveData(transactionalRequest)
        .then(data => {
          let pedido = data.registration.requestNumber;
          let mensaje = data.registration.message;
          let mensajeAddFiles = data.registration.messageAddFiles;
          let estado = data.registration.status;
          setSecondOpen(true);
          setResultRequest(pedido);
          setResultStatus(estado);
          setResultMsg(mensaje);
          setResultAddFiles(mensajeAddFiles);
          setLoading(false);
        });
    }
    // Fin Add Files (Agregar en todas las funcionalidades)
  }

  const validarDatos = () =>{
    let hayError = false;
    let exisCuentaPrincipalPesos = false;
    console.log(rows);  
    let cambioNoReg = rows.filter((row) => row.status.includes(row.denominacion) );
    if(cambioNoReg.length === rows.length){
      setMessage("No se registraron cambios");
      hayError = true;
    }
    /* Se verfica en la lista de cuentas si no hay cuentas corriente/ahorro en pesos como "cuenta principal"
    0: Cuenta Corriente en Pesos
    2: Caja de Ahorro en Pesos
    3: Caja de Ahorro el Dolares*/
    rows.forEach(row => {
      if(row.tipProduct!=="3" && row.denominacion=== "Cuenta Principal"){
        exisCuentaPrincipalPesos = true;
      }
    })
    if(!exisCuentaPrincipalPesos){
      setMessage("Falta seleccionar Cuenta Principal en pesos");
      hayError = true;
    }
    let i=0;
    rows.map((row,i) =>{
      if(row.denominacion !== "Sin Asignar" && row.indivEmpres === "1"){
        setMessage("No se puede relacionar cuentas de Naturaleza Jurdica - " + row.detProduct +" "+ row.product);
        hayError = true;
      }else if(row.denominacion !== "Sin Asignar" && (row.ordConj === "3" || row.ordConj === "6")){
        setMessage("No se puede relacionar cuentas de Orden Conjunta - " + row.detProduct +" "+ row.product);
        hayError = true;
      }else if(row.denominacion === "Sin Asignar" && row.status === "Cuenta Principal"){
        setMessage("No se puede desvincular una cuenta principal, debe asignarla como primaria - " + row.detProduct +" "+ row.product);
        hayError = true;
      }else if(row.denominacion !== "Cuenta Principal" && row.status.includes("Cuenta Principal") && row.tipProduct === "3"){
        setMessage("La " + row.detProduct +" "+ row.product +" que actualmente est asignada como Principal, no puede ser cambiada. Consultar el procedimiento a seguir para estos casos");
        hayError = true;
      }
    });
    console.log(rows);

    if (hayError){
      setOpenSnackbar(true);
      setSeverity('error');
      return;
      // cuenta de dolares y pesos anterior y actual
    }else{
       let ctaPppalAntP = new Object();
       let ctaPppalAntD = new Object(); 
       let ctaPppalActP = new Object();
       let ctaPppalActD = new Object(); 
       ctaPppalAntP.descripcion = "";
       ctaPppalAntP.codigo = "";
       ctaPppalAntD.descripcion = "";
       ctaPppalAntD.codigo = ""; 
       ctaPppalActP.descripcion = "";
       ctaPppalActP.codigo = ""; 
       ctaPppalActD.descripcion = "";
       ctaPppalActD.codigo = "";  
       
      // posicion de cuenta anterior-actual findIndex pesos
      let posCtaPrinPesosAnt = rows.findIndex( rw =>  rw.tipProduct !== "3" && rw.status.includes("Cuenta Principal"));
      console.log(posCtaPrinPesosAnt);
      if(posCtaPrinPesosAnt >-1 ){
        console.log( "posicion cuenta principal anterior pesos: "+ posCtaPrinPesosAnt );
        ctaPppalAntP.descripcion = rows[posCtaPrinPesosAnt].detProduct;
        ctaPppalAntP.codigo = rows[posCtaPrinPesosAnt].product;

        
        
      }
      
      let posCtaPrinPesosAct = rows.findIndex( rw =>  rw.tipProduct !== "3" && rw.denominacion === "Cuenta Principal");
      if(posCtaPrinPesosAct >-1){
        console.log( "posicion cuenta principal actual pesos: "+ posCtaPrinPesosAct );
          ctaPppalActP.descripcion = rows[posCtaPrinPesosAct].detProduct;
          ctaPppalActP.codigo = rows[posCtaPrinPesosAct].product;
      }
      

      // posicion de cuenta anterior-actual findIndex dolares
      let posCtaPrinDolAnt = rows.findIndex( rw =>  rw.tipProduct === "3" && rw.status.includes("Cuenta Principal"));
      if(posCtaPrinDolAnt >-1){
        console.log( "posicion cuenta principal anterior Dolares: "+ posCtaPrinDolAnt );
        ctaPppalAntD.descripcion = rows[posCtaPrinDolAnt].detProduct;
      ctaPppalAntD.codigo = rows[posCtaPrinDolAnt].product;
      }
      

      let posCtaPrinDolAct = rows.findIndex( rw =>  rw.tipProduct === "3" && rw.denominacion === "Cuenta Principal");
      if(posCtaPrinDolAct >-1){
        console.log( "posicion cuenta principal anterior Dolares: "+ posCtaPrinDolAct );
        ctaPppalActD.descripcion = rows[posCtaPrinDolAct].detProduct;
      ctaPppalActD.codigo = rows[posCtaPrinDolAct].product;
      }
      
     

      // no hay principal inicialmente
      if(ctaPppalActD.codigo !=="" && ctaPppalAntD.codigo ==="" ){
        ctaPppalAntD.codigo = ctaPppalActD.codigo;
        ctaPppalAntD.descripcion = ctaPppalActD.descripcion;
      }
      if(ctaPppalActP.codigo !=="" && ctaPppalAntP.codigo ===""){
        ctaPppalAntP.codigo = ctaPppalActP.codigo;
        ctaPppalAntP.descripcion = ctaPppalActP.descripcion;
      }

      
    console.log("Cuenta principal anterior en Pesos " + ctaPppalAntP.descripcion + " " + ctaPppalAntP.codigo );
     console.log("Cuenta principal actual en Pesos " +  ctaPppalActP.descripcion + " " + ctaPppalActP.codigo );
     console.log("Cuenta principal anterior en Dolares" + ctaPppalActD.descripcion + " " + ctaPppalActD.codigo );
     console.log("Cuenta principal actual en Dolares" + ctaPppalActD.descripcion + " " + ctaPppalActD.codigo );

     
   

     // verificar modificaciones en el ABM
    let historialObservaciones = [];
     rows.forEach(row => {

      let tipoCuenta ="";
      let description="";
      let codigo="";
      let accttypecde1="";
      let ajuste ="";
      let observaciones ="";
      let observacion ="";
      
      if(row.detProduct === "Caja Ahorros $"){
        tipoCuenta = "CA $";
      }else if(row.detProduct === "Caja Ahorros u$s"){
        tipoCuenta = "CA u$s";
      }else if(row.detProduct === "Cuenta Corriente $"){
        tipoCuenta = "CC $";
      } 
      row.tipoCuenta = tipoCuenta;

      observaciones = "\nRELACIONAR CUENTAS - PRODUCTO: " + productNumber +"\n" ;
       //ALTA
      if(row.status.includes("Sin Asignar")  && row.denominacion !== "Sin Asignar" ){

        codigo ="1";
        description ="A";
        ajuste = "1";

        observacion = "Vinculacion de la Cuenta: " + row.tipoCuenta + " " +  row.account;
        observaciones = observaciones +" "+  observacion;
        console.log(observaciones);
        historialObservaciones.push = observaciones;   
        //Codigo de estado Cuenta Principal 
       if(row.denominacion === "Cuenta Principal"){

          accttypecde1 = "3";// Cuenta Principal

       }else
       {
        accttypecde1 = "1";//Cuenta Primaria
       }     

         //BAJA
         
      }else if(!row.status.includes("Sin Asignar") && row.denominacion === "Sin Asignar"){
            console.log(row.denominacion === "Sin Asignar");
        codigo ="2";
        description ="B";
        ajuste = "2";
        observacion = "Desvinculacion de la Cuenta: " + row.tipoCuenta + " " +  row.account;
        observaciones = observaciones +" "+  observacion;
        historialObservaciones.push = observaciones;  

            //MODIFICACION
        }else if(!row.status.includes(row.denominacion) && row.denominacion !=="Sin Asignar"  && !row.status.includes("Sin Asignar")){
          
          codigo ="3";
          description ="M";
          ajuste = "3"; 
          
          if(row.tipProduct !== "3"){
            if(ctaPppalAntP.codigo!=="" && ctaPppalActP.codigo!=="" && ctaPppalActP.codigo !== ctaPppalAntP.codigo){
              observacion = "Modificacion de Cuenta Principal: \n Cuenta Nueva ($): " +
               ctaPppalActP.descripcion + " " +ctaPppalActP.codigo + "\n Cuenta Anterior ($): " + 
               ctaPppalAntP.descripcion + " " + ctaPppalAntP.codigo;
            }else if(ctaPppalAntP.codigo!=="" && ctaPppalActP.codigo!=="" && ctaPppalActP.codigo === ctaPppalAntP.codigo){
              observacion = "Modificacion de Cuenta Principal: \n Cuenta Nueva ($): " +
              ctaPppalActP.descripcion + " " +ctaPppalActP.codigo;
            }


          }else if(row.tipProduct === "3"){
            if(ctaPppalAntD.codigo!=="" && ctaPppalActD.codigo!=="" && ctaPppalActD.codigo !== ctaPppalAntD.codigo){
              observacion = "Modificacion de Cuenta Principal: \n Cuenta Nueva (u$s): " +
               ctaPppalActD.descripcion + " " +ctaPppalActD.codigo + "\n Cuenta Anterior (u$s): " + 
               ctaPppalAntD.descripcion + " " + ctaPppalAntD.codigo;
            }else if(ctaPppalAntD.codigo!=="" && ctaPppalActD.codigo!=="" && ctaPppalActD.codigo === ctaPppalAntD.codigo){
              observacion = "Modificacion de Cuenta Principal: \n Cuenta Nueva (u$s): " +
              ctaPppalActD.descripcion + " " +ctaPppalActD.codigo;
            }
            observaciones = observaciones +" "+  observacion;
                  // Evitar grabar el mismo registro en la modificacion
                  let cuentaRepetida = false;
                  historialObservaciones.forEach( ho => {
                    if(ho === observaciones){
                      cuentaRepetida =true
                      console.log("repetida");
                    }
                  })
                  if(!cuentaRepetida){
                    historialObservaciones.push = observaciones;

                  }
         }
         
        }
          row.codigo = codigo;
          row.description = description;
          row.ajuste = ajuste;
          row.accttypecde1 = accttypecde1;
          row.observaciones = observaciones;
         

    });
      getConfirmation();
    }
    
  } 
  const getConfirmation = () => {
    setFirstOpen(true);
  }

//Selec renderizando NUEVO  
  React.useEffect(() => {
    async function callAPI() {
     console.log(denom);
     let splitDenom = denom.split("_");
     let denomCta=splitDenom[0];
     let nroCta=splitDenom[1];
      console.log('[' + productNumber + ']')
    let ctaPrincipalPesos=false;
    let ctaPrincipalDolares=false;
    let hayError=false;
    if(denom===""){
      RelatedAccountService.consulta(operationId, documentType, documentNumber, productNumber)
        .then(data => {
          console.log(data.relatedAccounts);

          data.relatedAccounts.forEach(row =>{
            
            if(row.status !=="Cuenta Principal" && row.status !=="Cuenta Primaria" ){

              row.status = "Sin Asignar";
            }
             row.denominacion  = row.status;
             
            switch(row.status){
                case "Cuenta Principal":
                    row.status="3: "+ row.status;
                    break;
                case "Cuenta Primaria":
                    row.status="1: "+ row.status;
                    break;
                default:
                    row.status="0: "+ row.status;
                    break;
            }
            
            
         console.log(row.status);       
        });
          setRows(data.relatedAccounts);
          setLoading(false);
        });

    }else{
      let posCtaPrincipalPesos="";
      let posCtaPrincipalDolares="";
      posCtaPrincipalPesos=rows.findIndex((rw) => rw.denominacion==="Cuenta Principal" && rw.tipProduct!== "3");
      if(posCtaPrincipalPesos > -1){
        ctaPrincipalPesos=true;
      }
      posCtaPrincipalDolares=rows.findIndex((rwDol) =>rwDol.denominacion==="Cuenta Principal" && rwDol.tipProduct==="3");
      if(posCtaPrincipalDolares > -1){
        ctaPrincipalDolares=true;
      }
      console.log("posPesos " + posCtaPrincipalPesos);
      console.log("posDolares " + posCtaPrincipalDolares);
      rows.forEach((row,i) => {
        console.log(i);
        if(row.account===nroCta) {
                if(ctaPrincipalPesos && i!==posCtaPrincipalPesos && row.tipProduct!=="3")
                    {
                      if(denomCta==="Cuenta Principal"){
                        hayError=true;
                        setMessage("Ya existe una cuenta Principal");
                        console.log("Debe ser cuenta Primaria");
                        row.denominacion="Cuenta Primaria";
                        document.getElementById(row.product).innerHTML="Cuenta Primaria";
                      }else{
                      row.denominacion=denomCta;
                    }
                  
                  
                    }else if(ctaPrincipalDolares && i!==posCtaPrincipalDolares && row.tipProduct==="3"){
                      if(denomCta==="Cuenta Principal"){
                        hayError=true;
                        setMessage("Ya existe una cuenta Principal en Dolares");
                        console.log("Debe ser cuenta Primaria");
                        row.denominacion="Cuenta Primaria";
                        document.getElementById(row.product).innerHTML="Cuenta Primaria";
                      }else{
                        row.denominacion=denomCta;
                       }

                    }else{
                      row.denominacion=denomCta;
                     }

      }   
        
      })
      if(hayError){
        setOpenSnackbar(true);
        setSeverity('error');
        return;
      }
    }
    }
    callAPI();
 }, [operationId, documentType, documentNumber, productNumber, denom]);


  const printData = () => {
    setLoading(true);

    let commonParams = UtilsService.getCommonParams(operationId, productCode, causeCode, companyCode, documentType, documentNumber, businessName, productNumber, origin,
      user, option, contactModeCode, reasonCode, responsibleSector, registerSector, initContact, closeContact, resultRequest);

    let transactionalRequest = {}
    transactionalRequest.commonParams = commonParams;
    transactionalRequest.relatedAccountParams = rows;

    BaseService.printData(transactionalRequest)
      .then(data => {
        setLoading(false);
      });
  };
  const handleChange = (event, data) => {
   console.log(data.props.label);
   let valueSelect= data.props.label.split("-");
   console.log(valueSelect);
   

    
    rows.map((row) =>{
        if(row.product === valueSelect[1]){
          row.denominacion = valueSelect[0]; 
          console.log(valueSelect[1]);  
        }    
    })
    console.log(event.target.value);
    console.log(document.getElementById(valueSelect[1]));
    document.getElementById(valueSelect[1]).value="Sin Asignar";//valueSelect[0];
    //relatedAccount.denominacion=data.props.label;

}
  return (
    <div className={classes.root}>
      {/* Start Navbar Module */}
      <NavbarModule />
      {/* End Navbar Module */}

      <Container maxWidth="lg">
        <Grid container>
          <Grid item lg={12} style={{ width: '100%' }}>
            <Card className={classes.root} variant="outlined">
              <CardContent>
               <Typography variant="h6" component="h2" className={classes.paddingLine}>
                  <img src={line} className={classes.line} alt="logo" />
                  Cliente
                </Typography>

                <Grid container spacing={3} className={classes.spaceText}>
                  <Grid item lg={4}>
                    <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                      Apellido y Nombre
                    </Typography>
                    <Typography color="textSecondary">
                      {businessName}
                    </Typography>
                  </Grid>

                  <Grid item lg={2}>
                    <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                      Tipo y N de Documento
                    </Typography>
                    <Typography color="textSecondary">
                      {documentType} - {documentNumber}
                    </Typography>
                  </Grid>
                  <Grid item lg={2}>
                    <Typography variant="caption" display="block" gutterBottom className={classes.gutterBottom}>
                      N Banelco
                    </Typography>
                    <Typography color="textSecondary">
                      {productNumber}
                    </Typography>
                  </Grid>
                  <Grid item lg={4}>
                    <Typography color="textSecondary" className={classes.flex}>
                      P.TIT.ELECTRON
                    </Typography>
                  </Grid>
                </Grid>
                <Divider variant="middle" className={classes.divider} />

                <Typography variant="h6" component="h2" className={classes.paddingLine}>
                  <img src={line} className={classes.line} alt="logo" />
                  Lista de Cuentas - Producto: {productDescription}: {productNumber}
                </Typography>

                {rows && rows.length > 0 ?
                  <>
                    {/* start table data */}
                    <TableContainer component={Paper}>
                      <Table aria-label="customized table">
                        <TableHead>
                          <TableRow>
                            <StyledTableCell>Tipo</StyledTableCell>
                            <StyledTableCell>Nmero</StyledTableCell>
                            <StyledTableCell>Estado</StyledTableCell>
                            <StyledTableCell>Modificacion</StyledTableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {rows.map((row) =>
                           //<RelatedAccountRow  relatedAccount={row}></RelatedAccountRow>
                           <StyledTableRow>
                              <StyledTableCell>{row.detProduct}</StyledTableCell>
                              <StyledTableCell>{row.account}</StyledTableCell>
                              <StyledTableCell>{row.status}</StyledTableCell>
                              <StyledTableCell>
                                <FormControl className={classes.formControl} >
                                    <Select id = {row.product} style={{ maxWidth: '200px', minWidth: '120px' }} defaultValue={row.denominacion ? row.denominacion:" "}>
                                    <MenuItem onClick={() => setDenom("Sin Asignar_"+row.account)} value={"Sin Asignar"} key={"Sin Asignar"} >Sin Asignar</MenuItem>
                                    <MenuItem onClick={() => setDenom("Cuenta Primaria_"+row.account)}value={"Cuenta Primaria"} key={"Cuenta Primaria"}>Cuenta Primaria</MenuItem>
                                    <MenuItem onClick={() => setDenom("Cuenta Principal_"+ row.account)} value={"Cuenta Principal"}key={"Cuenta Principal"}>Cuenta Principal</MenuItem>
                                  </Select>
                                </FormControl>
                              </StyledTableCell>
                                
                            </StyledTableRow> 
                          )}
                        </TableBody>
                      </Table>
                    </TableContainer>
                    {/* end table data */}
                  </>
                  : <div><b>No se encontraron Cuentas para el producto.</b></div>}
                {/* Inicio Add Files (Agregar en todas las funcionalidades) */}
                <DocumentalRelation isAddFiles={isAddFiles} setIsAddFiles={setIsAddFiles} setClientRelations={setClientRelations} setProductRelations={setProductRelations} />
                {isAddFiles ?
                  <UploadFile clientRelations={clientRelations} productRelations={productRelations}></UploadFile>
                  : <div></div>}
                {/* Fin Add Files (Agregar en todas las funcionalidades) */}
                <br></br>
                <HelpModule />
              </CardContent>
            </Card>
          </Grid>
          <CustomizedSnackbars openSnackbar={openSnackbar} severity={severity} message={message} setOpenSnackbar={setOpenSnackbar}></CustomizedSnackbars>
        </Grid>

        {/* Start Loading Module */}
        <LoadingModule loading={loading}></LoadingModule>
        {/* End Loading Module */}

        {/* Start Dialog */}
        <DialogsModule saveData={saveData} resultRequest={resultRequest} resultStatus={resultStatus}
          resultMsg={resultMsg} resultAddFiles={resultAddFiles} firstOpen={firstOpen} setFirstOpen={setFirstOpen} printScreen={printData}
          secondOpen={secondOpen} setSecondOpen={setSecondOpen}></DialogsModule>
        {/* End Dialog */}

        {/* Start Buttons Module */}
        <ButtonsModule disable={disable} getConfirmation={validarDatos} valueButton={"Procesar"}></ButtonsModule>
        {/* End Buttons Module */}
      </Container>
    </div>

  );

}