/* eslint-disable no-template-curly-in-string */
import React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import ReprintModule from './components/reprintModule/reprintModule';
import DischargeModule from './components/dischargeModule/dischargeModule';
import ResetPinModule from './components/resetPinModule/resetPinModule';
import CloseChangeModule from './components/closeChangeModule/closeChangeModule';
import RelatedAccountModule from './components/relatedAccounModule/relatedAccountModule'
import AbmModule from './components/AbmModule/abmModule';
import RewardsModule from './components/rewardsModule/rewardsModule';
import LowAdditionalModule from './components/lowAdditionalModule/lowAdditionalModule';

import ChangeAddressTCModule from './components/changeAddressTCModule/changeAddressTC';
import EnableTDModule from './components/enableTDModule/enableTD';
import OCAForwardingModule from './components/OCAForwardingModule/OCAForwarding';
import AdditionalUpperModule from './components/additionalUpperModule/additionalUpper';
import LowTCModule from './components/lowTCModule/lowTC';
import ReprintTCModule from './components/reprintTCModule/reprintTC';
import AccountCLosingModule from './components/accountClosingModule/accountClosing';
import LowAdditionalTCModule from './components/lowAdditionalTCModule/lowAdditionalTC';

function App() {

  return (
    <Router basename={'/transaccional'}>
      <div>
        <Switch>
          <Route path={`/reprint/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={Reprint} />
          <Route path={`/discharge/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={Discharge} />
          <Route path={`/resetPin/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={ResetPin} />
          <Route path={`/closeChange/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={CloseChange} />
          <Route path={`/relatedAccount/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={RelatedAccount} />
          <Route path={`/abm/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={ABM} />
          <Route path={`/rewards/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={Rewards} />
          <Route path={`/lowAdditional/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={LowAdditional} />
          <Route path={`/changeAddressTC/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={ChangeAddressTC} />
          <Route path={`/enableTD/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={EnableTD} />
          <Route path={`/OCAForwarding/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={OCAForwarding} />
          <Route path={`/AdditionalUpper/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={AdditionalUpper} />
          <Route path={`/LowTC/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={LowTC} />
          <Route path={`/ReprintTC/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={ReprintTC} />
          <Route path={`/AccountClosing/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={AccountClosing} />
          <Route path={`/LowAdditionalTC/:operationId/:option/:documentType/:documentNumber/:businessName/:contactModeCode/:productCode/:causeCode/:reasonCode/:companyCode/:responsibleSector/:registerSector/:user/:origin/:initContact/:closeContact/:productNumber`} component={LowAdditionalTC} />
        </Switch>
      </div>
    </Router>
  );
}

export default App

function Reprint() {
  return <ReprintModule></ReprintModule>;
}

function Discharge() {
  return <DischargeModule></DischargeModule>;
}

function ResetPin() {
  return <ResetPinModule></ResetPinModule>;
}

function CloseChange() {
  return <CloseChangeModule></CloseChangeModule>;
}

function RelatedAccount() {
  return <RelatedAccountModule></RelatedAccountModule>;
}

function ABM() {
  return <AbmModule></AbmModule>;
}

function Rewards() {
  return <RewardsModule></RewardsModule>;
}

function LowAdditional() {
  return <LowAdditionalModule></LowAdditionalModule>;
}

function ChangeAddressTC() {
  return <ChangeAddressTCModule></ChangeAddressTCModule>
}

function EnableTD() {
  return <EnableTDModule></EnableTDModule>
}

function OCAForwarding() {
  return <OCAForwardingModule></OCAForwardingModule>
}

function AdditionalUpper() {
  return <AdditionalUpperModule></AdditionalUpperModule>
}

function LowTC() {
  return <LowTCModule></LowTCModule>
}

function ReprintTC() {
  return <ReprintTCModule></ReprintTCModule>
}

function AccountClosing() {
  return <AccountCLosingModule></AccountCLosingModule>
}

function LowAdditionalTC() {
  return <LowAdditionalTCModule></LowAdditionalTCModule>
}
