import UtilsService from "./utilsService";

const RelatedAccountService = {

    consulta(operationId, tipDoc, documento, producto) {
        return UtilsService._call_get(UtilsService.URL() + '/relacionamiento/cuentas/consulta?operationId=' + operationId + '&tipDoc=' + tipDoc + '&documento=' + documento +  '&producto=' + producto);
    }

}

export default RelatedAccountService;